<template>
  <div>{{data}}</div>
</template>
<script>
import axios from 'axios' // 引入axios
export default {
  // 挂载的时候获取新闻列表
  data () {
    return {
      data: {}
    }
  },
  mounted () {
    axios.get('/mock/news').then(res => { // url即在mock.js中定义的
      console.log(res.data) // 打印一下响应数据
      this.data = res.data
    })
  }
}
</script>



